import React, { useState } from 'react';
import { NavBar, Cell, Button, Form, Field, Toast, Dialog } from 'react-vant';
import { useNavigate } from 'react-router-dom';
import Icon from '../components/Icon';
import { useAuth } from '../context/AuthContext';
import { authAPI } from '../services/api';

const Profile = () => {
  const navigate = useNavigate();
  const { user, logout } = useAuth();
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);

  const handleLogout = () => {
    Dialog.confirm({
      title: '',
      message: '',
    }).then(() => {
      logout();
      navigate('/login', { replace: true });
    }).catch(() => {});
  };

  const handleChangePassword = async (values) => {
    if (values.newPassword !== values.confirmPassword) {
      Toast.fail('');
      return;
    }
    try {
      setLoading(true);
      await authAPI.changePassword({
        oldPassword: values.oldPassword,
        newPassword: values.newPassword,
      });
      Toast.success('');
      form.resetFields();
    } catch (error) {
      Toast.fail(error?.response?.data?.message || '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <div className="card" style={{
          background: '#007AFF',
          color: 'white',
          padding: '24px 16px',
          marginBottom: '16px'
        }}>
          <div style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '16px'
          }}>
            <div style={{
              width: '64px',
              height: '64px',
              background: 'rgba(255, 255, 255, 0.2)',
              borderRadius: '50%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              marginRight: '16px',
              fontSize: '28px'
            }}>
              <Icon name="user-o" />
            </div>
            <div>
              <div style={{ fontSize: '22px', fontWeight: 600, marginBottom: 4 }}>
                {user?.username || user?.user_id || ''}
              </div>
              <div style={{ fontSize: 15, opacity: 0.9 }}>
                ID: {user?.user_id || '-'}
              </div>
            </div>
          </div>
          
          <Button
            size="small"
            round
            style={{
              background: 'white',
              border: 'none',
              color: '#007AFF',
              fontWeight: 500
            }}
            onClick={handleLogout}
          >
            
          </Button>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Form form={form} layout="vertical" onFinish={handleChangePassword}>
            <Form.Item
              name="oldPassword"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Field type="password" placeholder="" />
            </Form.Item>
            <Form.Item
              name="newPassword"
              label=""
              rules={[{ required: true, message: '' }, { min: 6, message: '6' }]}
            >
              <Field type="password" placeholder=" 6 " />
            </Form.Item>
            <Form.Item
              name="confirmPassword"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Field type="password" placeholder="" />
            </Form.Item>
            <Button
              type="primary"
              block
              loading={loading}
              nativeType="submit"
            >
              
            </Button>
          </Form>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Cell.Group inset={false}>
            <Cell
              title=""
              label=""
              icon={<Icon name="point-gift-o" color="#007AFF" />}
              isLink
              onClick={() => navigate('/points-query')}
            />
            <Cell
              title=""
              label=""
              icon={<Icon name="orders-o" color="#007AFF" />}
              isLink
              onClick={() => navigate('/exchange-record')}
            />
          </Cell.Group>
        </div>
      </div>
    </div>
  );
};

export default Profile;
